import { model, Schema, models } from 'mongoose';

export default function getshortagesModel() {
  const collectionName = 'shortages';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const shortagesSchema = new Schema({
    serialNumber: {
      type: String,
      required: true,
    },
    caseBarCode: {
      type: String,
      required: true,
    },
    productionCode: {
      type: String,
      required: true,
    },
    healCode: {
      type: String,
      required: true,
    },
    productionName: {
      type: String,
      required: true,
    },
    batchNumber: {
      type: String,
      required: true,
    },
    date: {
      type: Date,
      required: true,
    },
  });

  return model(collectionName, shortagesSchema);
}
